export default {
    data() {
        return {
            specsArr: [{
                name: '一个月',
                price: 1000,
                id: 1
            },{
                name: '三个月',
                price: 3000,
                id: 2
            },{
                name: '十二个月',
                price: 12000,
                id: 3
            }],
            skuId: 1
        }
    },
    filters: {
        priceText(price) {
            return (price/100).toFixed(1)
        }
    },
    methods: {
        choseItem (item) {
            this.skuId = item.id
        },
        gotoExchange () {
            uni.navigateTo({
                url: '/pages/exchangePage/exchangePage'
            })
        },
        handlePay () {

        }
    },
}
